/****** reset ***************/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

html, body {
  padding: 0;
  height: 100%;
}

body {
    line-height:1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {
    display:block;
}

nav ul {
    list-style:none;
}

blockquote, q {
    quotes:none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content:'';
    content:none;
}

a {
    margin:0;
    padding:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

ins {
    background-color:#ff9;
    color:#000;
    text-decoration:none;
}

mark {
    background-color:#ff9;
    color:#000;
    font-style:italic;
    font-weight:bold;
}

del {
    text-decoration: line-through;
}

abbr[title], dfn[title] {
    border-bottom:1px dotted;
    cursor:help;
}

table {
    border-collapse:collapse;
    border-spacing:0;
}

/* change border colour to suit your needs */
hr {
    display:block;
    height:1px;
    border:0;
    border-top:1px solid #cccccc;
    margin:1em 0;
    padding:0;
}

input, select {
    vertical-align:middle;
}


/****** styles for application *********/

html {
  font-family: 'Manjari', sans-serif;
  color: #2d3667;
}

/****** containers in the main grid *********/

.grid-container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 350px 280px 280px 1fr;
  grid-template-rows: 20px 160px 15px min-content min-content 1fr;
  background-color: #ebf3f3;
}

@media (max-width: 900px) {
  .grid-container {
    width: 900px;
  }
}

@media (max-height: 525px) {
  .grid-container {
    height: 525px;
  }
}

.grid-item-controls,
.grid-item-meta,
.grid-item-desc {
  background-color: #ebf3f3;
}

.grid-frames-cols,
.grid-item-frames-orig,
.grid-item-frames-anno {
  background-color: black;
  color: #fff;
}

.grid-item-controls {
  grid-column: 3 / 4;
  grid-row: 2 / 3;
  padding: 5px;
}

.grid-item-meta {
  grid-column: 4 / 5;
  grid-row: 2 / 3;
  border-radius: 25px;
  border: 2px solid #268bd2;
  background-color: #ffffff;
  padding: 10px;
  margin-bottom: 10px;
}

.grid-item-desc {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
}

.grid-frames-cols {
  grid-column: 2 / 5;
  grid-row: 3 / 4;
  display: grid;
  grid-template-columns: 20px repeat(5, 1fr);
  grid-template-rows: 1fr;
}

.grid-item-frames-orig {
  grid-column: 2 / 5;
  grid-row: 4 / 5;
  display: grid;
  grid-template-columns: 20px repeat(5, 1fr);
  grid-template-rows: 1fr;
}

.grid-item-frames-anno {
  grid-column: 2 / 5;
  grid-row: 5 / 6;
  display: grid;
  grid-template-columns: 20px repeat(5, 1fr);
  grid-template-rows: 1fr;
}

.grid-item-overlay {
  grid-column: 2 / 5;
  grid-row: 2 / 6;
  background-color: #000000;
  visibility: hidden;
  z-index: 10;
  color: white;
  position: relative;
}

.visible {
  visibility: visible;
}

/****** navigation *********/

.grid-item-controls > h1 {

}

.grid-item-controls > .video-select, .type-select {
  width: 250px;
  font-size: 11px;
  padding-top: 5px;
}

.grid-item-controls > input {
  width: 250px;
  height: 25px;
  display: block;
}

.grid-item-controls > button {
  background-color: #268bd2;
  border: 0;
  border-radius: 12px;
  color: white;
  padding: 5px 32px;
  margin-left: 35px;
  margin-right: 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  opacity: 0.9;
  transition-duration: 0.4s;
}

.grid-item-controls > button:disabled {
  opacity: 0.2;
}

/****** metadata panel *********/

.grid-item-meta > ul {
  list-style-type:none;
  font-size: 14px;
  padding-top: 6px;
}

.grid-item-meta > li {
}

.grid-item-meta > ul >li > span {
  color: #268bd2;
}

.grid-item-meta > h3 {
  text-align: center;
}

.grid-item-meta > p {
  padding: 25px;
  text-align: center;
  font-size: 12px;
}

.ul-show {
  opacity: 1;
  transition: 0.3s;
}

.ul-hide {
  opacity: 0;
  transition: 0.3s;
}

/****** description panel *********/

.grid-item-desc > h2 {
  font-size:20px;
}

.grid-item-desc > p {
  padding-top: 12px;
}

.grid-item-desc > p > a, a:hover, a:active, a:visited {
  text-decoration: none;
  color: #268bd2;
  font-weight: bold;
}

/****** frame nested grid items *********/

.grid-frame-item-col {
  text-align: center;
  font-size: 11px;
  padding-top: 5px;
}

.grid-frame-item-row {
  font-size: 11px;
  position: relative;
  display: inline-block;
}

.grid-frame-item-row-rot {
  top: 50%;
  left: 50%;
  padding-top: 5px;
  position: absolute;
  white-space:nowrap;
  transform: translateX(-50%) translateY(-50%) rotate(-90deg);
}

.grid-frame-item {
  position: relative;
  padding: 5px;
}

.grid-frame-item > .img-thumb {
  cursor: pointer;
  width: 100%;
  user-select: none;
}

.grid-frame-item > .svg-open {
  position: absolute;
  cursor: pointer;
  padding: 6px;
  color: white;
  fill: white;
  font-weight: bold;
  font-size: 5px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.4);
  top: 5px;
  right: 5px;
  user-select: none;
  visibility: hidden;
}

.grid-frame-item:hover > .svg-open {
  visibility: visible;
}

.grid-frame-item:active {
  transform: scale(0.99);
}

/****** overlay *********/

.grid-item-overlay > .overlay-img {
  height: 100%;
  max-width: 700px;
  user-select: none;
}

.grid-item-overlay > .svg-icon {
  position: absolute;
  cursor: pointer;
  padding: 6px;
  color: white;
  fill: white;
  font-weight: bold;
  font-size: 5px;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  background-color: rgba(0, 0, 0, 0.4);
  right: 5px;
  transition: transform .1s;
}

.grid-item-overlay > .svg-icon:hover {
  opacity: 0.7;
}

.grid-item-overlay > .svg-icon:active {
  transform: scale(0.9);
}

.grid-item-overlay > .inactive {
  opacity: 0.4;
  pointer-events: none;
}

.grid-item-overlay > .inactive:hover {
  color: #3f3f3f;
}

.grid-item-overlay > .inactive:active {
  color: #3f3f3f;
  transform: scale(1);
}

.svg-close,
.svg-prev,
.svg-next {
  user-select: none;
}

.svg-close {
  top: 5px;
}

.svg-prev {
  bottom: 5px;
}

.svg-next {
  bottom: 60px;
}
